import React, { useEffect, useState, useRef } from 'react';
import UsuarioForm from '../components/UsuarioForm';
import ConfirmDialog from '../components/common/ConfirmDialog';
import { usuarioService } from '../services/usuarioService';
import './ConsultaUsuariosPage.css';

const ConsultaUsuariosPage = () => {
  const [usuarios, setUsuarios] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mensagem, setMensagem] = useState('');
  const [editingUsuario, setEditingUsuario] = useState(null);
  const [deleteDialog, setDeleteDialog] = useState({ isOpen: false, usuario: null });
  const resetFormRef = useRef(null);

  useEffect(() => {
    loadUsuarios();
  }, []);

  const loadUsuarios = async () => {
    setLoading(true);
    try {
      const rows = await usuarioService.listar();
      setUsuarios(rows);
    } catch (e) {
      setMensagem(e.message || 'Falha ao carregar usurios');
      setTimeout(() => setMensagem(''), 3000);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (payload) => {
    try {
      setLoading(true);
      if (editingUsuario) {
        const updated = await usuarioService.atualizar(editingUsuario.idusuario, payload);
        setUsuarios((prev) => prev.map((u) => (u.idusuario === updated.idusuario ? updated : u)));
        setMensagem('Usurio atualizado com sucesso!');
        setEditingUsuario(null);
      } else {
        const saved = await usuarioService.cadastrar(payload);
        setUsuarios((prev) => [saved, ...prev]);
        setMensagem('Usurio cadastrado com sucesso!');
        if (resetFormRef.current) {
          resetFormRef.current();
        }
      }
    } catch (e) {
      setMensagem(e.message || 'Falha ao salvar usurio');
    } finally {
      setLoading(false);
      setTimeout(() => setMensagem(''), 3000);
    }
  };

  const handleEdit = (usuario) => {
    setEditingUsuario(usuario);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const handleCancel = () => {
    setEditingUsuario(null);
    if (resetFormRef.current) {
      resetFormRef.current();
    }
  };

  const handleDeleteClick = (usuario) => {
    setDeleteDialog({ isOpen: true, usuario });
  };

  const handleDeleteConfirm = async () => {
    if (!deleteDialog.usuario) return;
    
    const usuario = deleteDialog.usuario;
    const id = usuario.idusuario;
    
    try {
      setLoading(true);
      await usuarioService.excluir(id);
      setUsuarios((prev) => prev.filter((u) => u.idusuario !== id));
      setMensagem('Usurio excludo com sucesso!');
      setDeleteDialog({ isOpen: false, usuario: null });
    } catch (e) {
      setMensagem(e.message || 'Falha ao excluir usurio');
    } finally {
      setLoading(false);
      setTimeout(() => setMensagem(''), 3000);
    }
  };

  const handleDeleteCancel = () => {
    setDeleteDialog({ isOpen: false, usuario: null });
  };

  return (
    <div className="consulta-usuarios-page">
      <div className="page-container">
        <div className="card form-card">
          <div className="card-body">
            <h1>{editingUsuario ? 'Editar Usurio' : 'Cadastrar Novo Usurio'}</h1>

            {mensagem && <div className="alert alert-success">{mensagem}</div>}

            <UsuarioForm
              onSubmit={handleSubmit}
              onCancel={handleCancel}
              initialData={editingUsuario}
              mode={editingUsuario ? 'edit' : 'create'}
              onFormReset={(resetFn) => { resetFormRef.current = resetFn; }}
            />

            {loading && <div className="alert alert-info">Carregando...</div>}
          </div>
        </div>

        <h2 className="mt-4">Usurios cadastrados</h2>

        {usuarios.length === 0 ? (
          <p className="text-muted">Nenhum usurio cadastrado ainda.</p>
        ) : (
          <div className="table-responsive">
            <table className="table table-striped">
              <thead>
                <tr>
                  <th>Matrcula</th>
                  <th>Nome</th>
                  <th>Email</th>
                  <th>Telefone</th>
                  <th>Perfil</th>
                  <th>Aes</th>
                </tr>
              </thead>
              <tbody>
                {usuarios.map((u) => (
                  <tr key={u.idusuario}>
                    <td data-label="Matrcula">{u.matricula}</td>
                    <td data-label="Nome">{u.nome}</td>
                    <td data-label="Email">{u.email}</td>
                    <td data-label="Telefone">{u.telefone}</td>
                    <td data-label="Perfil">
                      <span className={`badge badge-${u.perfil?.perfil === 'Administrador' ? 'admin' : 'comum'}`}>
                        {u.perfil?.perfil || 'Comum'}
                      </span>
                    </td>
                    <td data-label="Aes">
                      <button className="btn btn-sm btn-outline-primary me-2" onClick={() => handleEdit(u)}>
                        Editar
                      </button>
                      <button className="btn btn-sm btn-outline-danger" onClick={() => handleDeleteClick(u)}>
                        Excluir
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>

      <ConfirmDialog
        isOpen={deleteDialog.isOpen}
        title="Excluir Usurio"
        message={`Tem certeza que deseja excluir o usurio "${deleteDialog.usuario?.nome}"? Esta ao no pode ser desfeita.`}
        onConfirm={handleDeleteConfirm}
        onCancel={handleDeleteCancel}
        confirmText="Excluir"
        cancelText="Cancelar"
        isDanger={true}
        showUndoTimer={true}
        undoTimeout={5}
      />
    </div>
  );
};

export default ConsultaUsuariosPage;
